@import '../../scss/palette';

.complexWrapper{
  width: 100vw;
  height: 100vh;
  background-color: $colorBlack;
  overflow: hidden;
  position: relative;
  padding-top: 30px;

  .complexMainLink{
    position: fixed;
    top: 30px;
    left: 30px;
    z-index: 10;

    a{
      display: block;
      padding: 15px 30px;
      letter-spacing: 2px;
      text-transform: uppercase;
      color: $colorWhite;
      font-size: 24px;
      text-decoration: none;
      position: relative;
      border: 1px solid white;
      border-radius: 4px;
      background: none;
      cursor: pointer;
      transition: .5s;
      overflow: hidden;

      &:before{
        content: '';
        position: absolute;
        left: 0;
        width: 100%;
        height: 0;
        background: white;
        z-index: -1;
        transition: .5s;
        top: 0;
        border-radius: 0 0 50% 50%;
      }

      &:hover{
        color: black;
        &:before {
          height: 180%;
        }
      }
    }
  }

  .complexTitle{
    font-size: 60px;
    text-align: center;
    color: $colorWhite;
    margin-bottom: 20px;
  }

  .complexItemsWrapper{
    height: 88%;
    display: flex;
    justify-content: center;
  }

  .complexItem{
    width: 85vw;
    height: 100%;
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    position: relative;
  }

  .complexItemTitle{
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    font-size: 60px;
    text-align: center;
    color: $colorWhite;
  }

  .imgsWrapper{
    display: flex;
    align-items: center;
    justify-content: space-around;

    .complexItemImg{
      max-width: 400px;
      max-height: 70%;
    }
  }
}
