@import '../../scss/palette';
@import '../../scss/mixins';

.pageOverlay{
  width: 100%;
  height: 100%;
  position: relative;
  &.flexPageOverlay{
    @include flex-item;
    &.justifyEnd{
      justify-content: flex-end;
    }
  }
}

.blockOverlay{
  width: 1342px;
  height: 100%;
  margin: 0 auto;
  padding: 35px 0;

  @include mediaMin(320){
    width: 80%;
  }

  @include mediaMin(768){
    width: 600px;
    padding: 6px 0;
  }

  @include mediaMin(960){
    width: 750px;
  }

  @include mediaMin(1024){
    width: 830px;
  }

  @include mediaMin(1280){
    width: 995px;
  }

  @include mediaMin(1600){
    width: 1140px;
  }
}


