@import "./src/scss/palette";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif, 'Rubik Mono One', sans-serif;
  outline: none;
}

h1{
  font-size: 60px;
  line-height: 64px;
  font-weight: bold;
  color: rgba($simpleBrandyRose, .7);
}

.page {
  width: 100%;
  height: 100%;

  &-wrapper {
    position: relative;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
  }
}

.simplePageTransition{
  background-color: $simpleRaven !important;
}


//slider styles

.awssld__content{
  background-color: transparent !important;
}

.aws-sld {
  &__content {
    .complexItemTitle {
      transform: translate3d(0, 0, 0);
      opacity: 1;
      transition: transform 0.45s cubic-bezier(0.15, 0.3, 0.15, 1), opacity 0.35s ease-out;
    }
    &--exit {
      .complexItemTitle {
        transition: transform 0.225s cubic-bezier(0.85, 0, 0.85, 0.7), opacity 0.4s ease-out;
      }
    }
    &--moveLeft {
      .complexItemTitle {
        transform: translate3d(-50px, 0, 0);
        opacity: 0;
      }
    }
    &--moveRight {
      .complexItemTitle {
        transform: translate3d(50px, 0, 0);
        opacity: 0;
      }
    }
  }
}