@import '../../scss/palette';
@import '../../scss/mixins';

.arrow{
  width: 50px;
  height: 70px;
  position: absolute;

  a{
    width: 100%;
    height: 100%;

    div{
      pointer-events: none;
    }
  }

  &.arrowHeader{
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
  }
}

.mobileHeaderTitle{
  display: none;
  @include mediaMax(960){
    position: fixed;
    top: 0;
    left: 0;
    z-index: 70;
    font-family: "Rubik Mono One", sans-serif;
    background-color: $colorWhite;
    font-size: 30px;
    font-weight: 900;
    text-transform: uppercase;
    color: $simpleBrandyRose;
    height: 60px;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-bottom: 1px solid $simpleChambray;

    .hamburgerMobile {
      width: 40px;
      height: 40px;
      cursor: pointer;
      transition: .3s ease-in-out;

      .borderWrapper {
        width: 100%;
        height: 100%;
        position: relative;
        border: 1px solid transparent;
        border-radius: 50%;
        transition: .3s ease-in-out;

        .spanWrapper {
          width: 100%;
          height: 100%;
          display: flex;
          flex-direction: column;
          align-items: center;
          padding: 12px 0 10px 2.5px;
          overflow: hidden;

          span {
            display: block;
            height: 2px;
            width: 70%;
            background-color: $simpleBrandyRose;
            margin-bottom: 4px;
            transform: scale(1);
            transition: 0.5s all ease;

            &:last-child {
              margin-bottom: 0;
            }
          }

          &:after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            border: 1px solid $simpleBrandyRose;
            border-radius: 4px;
            transition: .3s ease-in-out;
            z-index: -1;
          }

          &:hover:after {
            transform: rotate(45deg);
            border-radius: 50%;
          }
        }
      }
    }
  }
}

.section{
  width: 100%;
  height: 100vh;
  margin-bottom: 50px;

  &:last-child{
    margin-bottom: 0;
  }

  @include mediaMax(960){
    height: 74vh;
  }

  @include mediaMax(768){
    height: unset;
  }
}

.pageTitle{
  font-size: 73px;
  line-height: 76px;
  font-weight: bold;
  color: $simpleBrandyRose;
  text-transform: uppercase;
  margin: 0 auto;

  &.pageTitleCentered{
    text-align: center;
  }

  @include mediaMax(1600){
    font-size: 68px;
    line-height: 60px;
  }

  @include mediaMax(1280){
    font-size: 60px;
    line-height: 60px;
  }

  @include mediaMax(1024){
    font-size: 40px;
    line-height: 40px;
  }

  @include mediaMax(960){
    font-size: 35px;
    line-height: 35px;
  }

  @include mediaMax(768){
    font-size: 29px;
    line-height: 30px;
  }
}

.outlineBtn{
  width: 198px;
  padding: 8px 16px;
  border: 1px solid #d09a4e;
  border-radius: 4px;
  color: #d09a4e;
  background-color: #fff;
  font-size: 30px;
  font-weight: 400;
  cursor: pointer;
  transition: .3s ease-in-out;
  text-decoration: none;
  text-align: center;
  display: block;

  &:hover{
    background-color: $simpleBrandyRose;
    color: $colorWhite;
  }

  &.standardMarginTop{
    margin-top: 25px;
  }

  @include mediaMax(1280){
    width: 165px;
    font-size: 23px;
  }

  @include mediaMax(960){
    width: 135px;
    font-size: 14px;
  }
}

//MENU SIDE TITLE-------------------------------------
.menuSideTitle{
  position: absolute;
  display: flex;
  flex-direction: column;
  width: 94px;
  top: 100%;
  transform-origin: 0 0;
  transform: translateY(-50%);
  left: 3.5%;

  .menuSideTitleText{
    font-family: "Rubik Mono One", sans-serif;
    color: $colorWhite;
    font-size: 102px;
    line-height: 94px;
    font-weight: 900;
    text-transform: uppercase;
    width: 96vh;
    height: 94px;
    text-align: center;
    transform-origin: 0 0;
    transform: rotate(-90deg);
    position: relative;

    &.titleBrandyColor{
      color: $simpleBrandyRose;
    }

    .sideTitleDivider{
      position: absolute;
      bottom: -62px;
      left: 0;
      height: 1px;
      width: 100%;
      background-color: $colorWhite;

      &.secondSideTitleDivider{
        bottom: -289px;

        @include mediaMax(1600){
          display: none;
        }
      }

      &.dividerChambrayColor{
        background-color: $simpleChambray;
      }

      @include mediaMax(1680){
        bottom: -50px;
      }

      @include mediaMax(1280){
        bottom: -15px;
      }

      @include mediaMax(1024){
        bottom: -10px;
      }

      @include mediaMax(960){
        bottom: -7px;
      }
    }

    @include mediaMax(1600){
      font-size: 70px;
      line-height: 72px;
      height: 40px;
    }

    @include mediaMax(1280){
      font-size: 60px;
      line-height: 60px;
      height: 58px;
    }

    @include mediaMax(1024){
      font-size: 40px;
      line-height: 40px;
      height: 38px;
    }

    @include mediaMax(960){
      font-size: 35px;
      line-height: 35px;
      height: 33px;
      width: 93vh;
    }
  }

  @include mediaMax(1600){
    width: 40px;
    left: 1.5%;
  }

  @include mediaMax(1280){
    width: 40px;
    left: 1.5%;
  }

  @include mediaMax(1024){
    width: 40px;
    left: 1%;
  }

  @include mediaMax(960){
    display: none;
  }

  &.displayMobile{
    display:none;

    @include mediaMax(768){
      display: block;
      position: fixed;
    }
  }
}
//MENU SIDE TITLE*************************************


//DIVIDER--------------------------------------------------
.divider{
  width: 100%;
  height: 1px;
  background-color: $simpleChambray;
  margin: 25px 0;

  @include mediaMax(1600){
    margin: 25px 0;
  }

  @include mediaMax(1280){
    margin: 15px 0;
  }

  @include mediaMax(1024){
    margin: 15px 0;
  }

  @include mediaMax(960){
    margin: 7px 0;
  }

  @include mediaMax(768){
    margin: 10px 0;
  }
}
//DIVIDER***********************************************

//HAMBURGER--------

.hamburger{
  position: fixed;
  right: 4%;
  top: 50%;
  transform: translateY(-50%);
  width: 90px;
  height: 90px;
  cursor: pointer;
  transition: .3s ease-in-out;
  z-index: 100;

  .borderWrapper{
    width: 100%;
    height: 100%;
    position: relative;
    border: 1px solid transparent;
    border-radius: 50%;
    transition: .3s ease-in-out;

    .spanWrapper {
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 29px 0;
      overflow: hidden;

      span {
        display: block;
        height: 2px;
        width: 70%;
        background-color: $simpleBrandyRose;
        margin-bottom: 12px;
        transform: scale(1);
        transition: 0.5s all ease;

        &:last-child {
          margin-bottom: 0;
        }

        @include mediaMax(1024){
          margin-bottom: 12px;
        }

        @include mediaMax(960){
          margin-bottom: 11.5px;
        }
      }

      @include mediaMax(1024){
        padding: 29px 0;
      }

      @include mediaMax(960){
        padding: 16px 0;
      }
    }

    &:after{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border: 1px solid $simpleBrandyRose;
      border-radius: 4px;
      transition: .3s ease-in-out;
      z-index: -1;
    }

    &:hover:after{
      transform: rotate(45deg);
      border-radius: 50%;
    }
  }

  @include mediaMax(1600){
    right: 4%;
  }

  @include mediaMax(1280){
    right: 2%;
  }

  @include mediaMax(1024){
    right: 0.5%;
  }
  @include mediaMax(960){
    width: 65px;
    height: 65px;
  }

  @include mediaMax(960){
    display: none;
  }
}

//HAMBURGER***********************************************


//MENU-----------------------------------------------------

.navigation{
  position: fixed;
  left: -120%;
  top: 50%;
  width: 100%;
  height: 101%;
  z-index: 50;
  background-color: $simpleBrandyRose;
  transform: translateY(-50%);

  .mainNavigation{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;

    ul{
      list-style: none;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }

    .mainNavigationItem{
      font-size: 60px;
      margin-bottom: 25px;

      a{
        text-decoration: none;
        color: $colorWhite;
        font-weight: 700;
        display: block;
        transition: .5s ease-in-out;
        position: relative;

        &:before {
          content: "";
          @include underline-link();
          background-color: $colorWhite;
          transform: scaleX(0);
        }

        &:hover:before {
          content: "";
          @include underline-link();
          background-color: $colorWhite;
          transform: scaleX(1);
        }
      }

      &:nth-child(1n):after{
        content: '';
        pointer-events: none;
        display: flex;
        align-items: center;
        justify-content: center;
        position: fixed;
        color: rgba($colorWhite, .2);
        font-size: 102px;
        line-height: 94px;
        font-weight: 900;
        width: 816px;
        height: 228px;
        text-align: center;
        top: 50%;
        left:-3.8%;
        text-transform: uppercase;
        transform: translateY(-50%) rotate(-90deg);
        transition: .5s ease-in-out;
        opacity: 0;

        @include mediaMax(1600){
          display: none;
        }
      }

      &:hover:nth-child(1):after{
        opacity: 1;
        content: 'Немного статистики';
      }

      &:hover:nth-child(2):after{
        opacity: 1;
        content: 'Наши услуги';
      }

      &:hover:nth-child(3):after{
        opacity: 1;
        content: 'Консультация';
      }

      &:hover:nth-child(4):after{
        opacity: 1;
        content: 'Кейсы';
      }

      &:hover:nth-child(5):after{
        opacity: 1;
        content: 'Контакты';
      }

      &:hover:nth-child(6):after{
        opacity: 1;
        content: 'На главную';
      }

      @include mediaMax(1024){
        font-size: 40px;
      }
    }
  }
}

