//SERVICE--------------------------------------------------
.service{
  position: relative;

  @include mediaMax(960){
    min-height: 100vh;
    height: unset;
  }
}

.serviceWrapper{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 50px 50px;
  align-items: center;
  justify-items: center;
  grid-template-areas:
    ". . ."
    ". . .";

  @include mediaMax(1024){
    gap: 10px 10px;
  }

  @include mediaMax(960){
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }
}

.box{
  position: relative;
  width: 332px;
  height: 332px;
  text-align: center;
  overflow: hidden;

  .boxContent{
    position: absolute;
    top: 15px;
    left: 15px;
    bottom: 15px;
    right: 15px;
    border: 1px solid $simpleBrandyRose;
    border-radius: 4px;
    padding: 15px;
    text-align: center;
    box-shadow: 0 5px 10px rgba($colorWhite, 0.5);
    .boxContentHeader{
      font-size: 28px;
      font-weight: 400;
      color: #0D0A49;
      line-height: 40px;
      height: 80px;

      @include mediaMax(1600){
        font-size: 24px;
        line-height: 28px;
      }

      @include mediaMax(1280){
        font-size: 19px;
        line-height: 23px;
        height: 48px;
      }

      @include mediaMax(1024){
        font-size: 19px;
        line-height: 23px;
        height: 48px;
      }

      @include mediaMax(960){
        font-size: 19px;
        line-height: 23px;
        height: 48px;
      }

      @include mediaMax(768){
        font-size: 19px;
        line-height: 23px;
        height: 48px;
      }
    }

    .boxPrice{
      margin-top: 25px;
      font-size: 36px;
      color: $simpleSandai;
      font-weight: 900;
      background: rgba(255,255,255,.1);

      @include mediaMax(1600){
        margin-top: 8px;
        font-size: 33px;
      }

      @include mediaMax(1280){
        margin-top: 22px;
        font-size: 23px;
      }

      @include mediaMax(768){
        margin-top: 22px;
        font-size: 23px;
      }
    }

    .boxBtn{
      position: absolute;
      bottom: 15px;
      left: 50%;
      transform: translateX(-50%);
      margin: 0 auto;
    }
  }

  .boxEl {
    width: 100%;
    height: 100%;
    display: block;
    position: absolute;

    &:before {
      content: '';
      position: absolute;
      left: 0;
      width: 100%;
      height: 1px;
      background: transparent;
      opacity: 0;
      transition: opacity .3s;
    }
  }

  &:hover{
    .boxEl{
      &:before{
        background: $simpleChambray;
        transition: opacity .3s;
        opacity: 1;
        animation: box-border 4s linear infinite;
      }

      &:nth-child(1){
        transform: rotate(0deg);
      }
      &:nth-child(2){
        transform: rotate(90deg);
        &:before{
          animation-delay: -2s;
        }
      }
      &:nth-child(3){
        transform: rotate(180deg);
      }
      &:nth-child(4){
        transform: rotate(270deg);
        &:before{
          animation-delay: -2s;
        }
      }
    }
  }

  @include mediaMax(1600){
    width: 294px;
    height: 294px;
  }

  @include mediaMax(1280){
    width: 240px;
    height: 240px;
  }
}
@keyframes box-border {
  0%{
    transform: scaleX(0);
    transform-origin: left;
  }
  50%{
    transform: scaleX(1);
    transform-origin: left;
  }
  50.1%{
    transform: scaleX(1);
    transform-origin: right;
  }
  100%{
    transform: scaleX(0);
    transform-origin: right;
  }
}
