      line-height: 26px;
    }

    @include mediaMax(1280){
      font-size: 25px;
      line-height: 26px;
    }

    @include mediaMax(1024){
      font-size: 22px;
      line-height: 26px;
    }
    @include mediaMax(960){
      font-size: 16px;
      line-height: 20px;
    }

    @include mediaMax(768){
      font-size: 15px;
      line-height: 16px;
      text-align: center;
    }
  }

  .consultationPrice{
    font-size: 36px;
    color: #af856b;
    font-weight: 900;
    margin-top: 25px;

    @include mediaMax(960){
      margin-top: 10px;
      font-size: 26px;
    }

    @include mediaMax(768){
