//CONTACTS--------------------------------------------------

.contacts {
  margin-bottom: 0;

  .contactsWrapper {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .lottieContactsWrapper {
      width: 55%;

      div{
        pointer-events: none;
      }

      @include mediaMax(768){
        width: 280px;
        height: 280px;
      }
    }

    @include mediaMax(960){
      margin-top: 25px;
    }

    @include mediaMax(768){
      flex-direction: column;
    }
  }

  .formAndSocialsWrapper{
    width: 40%;
    display: flex;
    flex-direction: column;

    .actionCall{
      font-size: 30px;
      line-height: 32px;
      font-weight: 400;
      color: $simpleBrandyRose;

      @include mediaMax(1600){
        font-size: 24px;
        line-height: 28px;
      }

      @include mediaMax(1280){
        font-size: 20px;
        line-height: 24px;
      }

      @include mediaMax(1024){
        font-size: 18px;
        line-height: 22px;
      }

      @include mediaMax(960){
        font-size: 13px;
        line-height: 17px;
      }

      @include mediaMax(768){
        font-size: 15px;
        line-height: 17px;
        text-align: center;
      }
    }

    .socialsWrapper{
      display: flex;
      justify-content: space-between;
      align-items: center;

      .socials{
        display: flex;

        .socialLink{
          color: $simpleBrandyRose;
          width: 36px;
          height: 36px;
          display: block;
          margin-right: 25px;
          transform-origin: 50% 50%;
          transition: .3s;

          svg{
            width: 30px;
            height: 30px;
          }

          &:last-child{
            margin-right: 0;
          }

          &:hover{
            transform: scale(1.2);
          }

          @include mediaMax(1280){
            margin-right: 5px;
          }

          @include mediaMax(960){
            width: 30px;
            height: 30px;
          }

          @include mediaMax(768){
            width: 30px;
            height: 30px;
            margin-right: 5px;
          }
        }
      }
    }

    .form{
      display: flex;
      flex-direction: column;
      margin: 25px 0;

      .inputWrapper {
        position: relative;

        .formInput {
          border: none;
          background-color: transparent;
          font-style: normal;
          font-weight: 300;
          font-size: 18px;
          line-height: 29px;
          width: 100%;
          height: 60px;
          border-bottom: 2px rgba($simpleBrandyRose, .9) solid;
          color: $simpleBrandyRose !important;

          -webkit-border-radius: 0;
          -webkit-box-shadow: none;
          border-radius: 0;
          box-shadow: none;

          &:-moz-placeholder {
            text-indent: 0;
            transition: text-indent 0.3s ease;
          }

          &:focus::-webkit-input-placeholder {
            text-indent: 100px;
            opacity: 0;
            transition: 0.6s;
          }

          &::placeholder {
            color: rgba($simpleBrandyRose, .5);
          }

          &.formInputError {
            border-color: #ae4945;
            color: #ae4945;
            -webkit-text-fill-color: #ae4945 !important;

            &::placeholder {
              color: #ae4945;
            }
          }

          @include mediaMax(960){
            font-size: 13px;
            height: 40px;
          }
        }

        .errorMsg {
          position: absolute;
          right: 0;
          top: 0;
          font-size: 14px;
          color: darkred;
        }
      }

      @include mediaMax(1280){
        margin-top: 10px;
      }
    }

    @include mediaMax(768){
      width: 100%;
    }
  }
}

